/*
SPACING SYSTEM (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color: hsl(30, 54%, 90%);
  line-height: 1.5;
  font-family: "Outfit", sans-serif;
  /* width: 10000px; */
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #eee;
  margin: 0.2rem 0;
  padding: 0;
}

ul,
ol {
  padding-left: 2.2rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.point-header {
  font-weight: 700;
}
ul {
  list-style: square;
}
li {
  padding-left: 1.7rem;
}

ul li::marker {
  font-size: small;
}

ol li::marker {
  font-weight: 700;
}

.app-body {
  max-width: 73.6rem;
  background-color: #fff;
  margin: 12.3rem auto;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  gap: 3.4rem;
  color: #666;
  font-size: 1.6rem;
}

.article-main header {
  padding: 4rem;
}

.ingredients-title,
.instructions-title,
.nutrition-title,
.value,
li::marker {
  color: hsl(14, 45%, 36%);
}

.ingredients-title,
.instructions-title,
.nutrition-title {
  font-size: 2.85rem;
  font-weight: 400;
  font-family: "Young Serif", serif;
  margin-bottom: 1.5rem;
}

.main-header {
  display: flex;
  flex-direction: column;
  gap: 2.7rem;
}

.header-img-container {
  margin: 4rem 4rem 0 4rem;
}

.header-img {
  width: 100%;
  border-radius: 12px;
}

.header-div {
  padding: 0 4rem;
}

.main-header-title {
  font-family: "Young Serif", serif;
  font-weight: normal;
  color: #444;
  font-size: 4rem;
  margin-bottom: 1.1rem;
  transform: scaleY(0.99);
}

.article-main {
  padding: 0 4rem 4rem 4rem;
  display: flex;
  flex-direction: column;
  gap: 2.6rem;
}

.preparation {
  background-color: hsl(330, 100%, 98%);
  padding: 2.2rem 0 3rem 2.9rem;
  border-radius: 11px;
}

.preparation-title {
  color: hsl(332, 51%, 32%);
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.nutrient-table {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.nutrient-table hr {
  grid-column: 1 / -1;
}

.nutrient-table .nutrient {
  padding-left: 2rem;
}

/* 48em = 48 * 16 = 768px */
@media (max-width: 48em) {
  html {
    font-size: 37.5%;
  }
}

/* 25em = 25 * 16 = 400px */
@media (max-width: 29.25em) {
  .app-body {
    border-radius: 0;
    margin: 0;
    padding: 0;
  }

  .preparation {
    margin-bottom: 3.2rem;
  }

  .header-img-container {
    margin: 0;
  }

  .header-img {
    border-radius: 0;
  }
}
